@media screen and (min-width: 100px) and (max-width: 500px) {
  .main {
    width: 100%;
   
  }
  .description {
    width: 75%;
    height: 500px;
    grid-template-columns: repeat(1, 1fr);
  }
  .rani {
    height: 20%;
  }

  .lines {
    /* height: 100%; */
    margin-top: -750px;
  }
  .lines > p {
    font-size: 13px;
  }
  .resume {
    height: 40%;
    font-size: 14px;
  }
}
