.main {
  width: 100%;
  height: 600px;
  margin-top: 8px;
  overflow-y: hidden;
  text-align: center;
}

.contactImg {
  width: 100%;
  height: 100%;
}

.subDiv {
  position: absolute;
  width: 50%;
  height: 50%;
  margin-left: 380px;
  margin-top: 20px;
}

.get {
  color: white;
  text-align: center;
  margin-top: 30px;
}

.form {
  margin-top: 30px;
}

.formDiv {
  margin-top: 20px;
}

.inputDiv {
  height: 40px;
  width: 50%;
  padding: 20px;
  color: black;
  font-size: 20px;
}

.inputDiv:focus {
  box-shadow: rgba(30, 144, 255) 0px 5px 15px;
  outline: none;
}

.inputDiv:not(:focus) {
  box-shadow: none;
}

.submit {
  border: none;
  background-color: rgba(30, 144, 255);
  margin-top: 30px;
  width: 90px;
  height: 30px;
}

.gmailDiv {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}

.gmail {
  height: 40px;
}

.btn {
  border: none;
  padding: 10px;
  height: 30px;
  text-align: center;
  margin: 5px;
  backdrop-filter: invert(80%);
  cursor: pointer;
}

.linkedin {
  height: 40px;
}

.github {
  height: 50px;
}

.align {
  width: 60%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  height: 80px;
  margin-top: 30px;
  align-items: center;
  justify-content: center;
  margin-left: 170px;
}

@media screen and (min-width: 500px) and (max-width: 1000px) {
  .subDiv {
    height: 450px;
    width: 100%;
    margin-left: -0.1%;
    padding: 50px;
  }
}

@media screen and (min-width: 100px) and (max-width: 500px) {
  .subDiv {
    height: 450px;
    width: 100%;
    margin-left: -0.1%;
    padding: 50px;
  }
  .align {
    grid-template-columns: repeat(1, 1fr);
    height: 140px;
    width: 100%;
    margin-left: -0.01px;
    gap: -2px;
    margin-top: 20px;
  }
  .gmail {
    display: none;
  }
  .btn {
     margin-left: 64px;
  }
  .linkedinDiv > a > img {
    margin-top: 10px;
  }
  .githubDiv > a > img {
    margin-top: 10px;
  }
  .inputDiv{
    width: 100%;
  }
}
