

.main {
  box-shadow:  rgba(30, 144, 255) 0px 5px 10px;
  background-color: black;
  color: white;
  margin-top: 61px;
  height: 650px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  /* overflow-y: hidden; */
}

.girl {
  height: 650px;
  width: 100%;
}

.head {
  text-align: center;
  width: 80%;
  margin-top: 7em;
  overflow: hidden;
  margin-left:3em;
  border-right: 0.15em solid #1e90ff;
  white-space: nowrap;
  letter-spacing: 0.15em;
  animation: typing 3.5s steps(30,end), blink-caret 0.5s step-end infinite;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 40%;
  }
}
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  30% {
    border-color: #1e90ff;
  }
}

@media screen and (min-width:500px) and (max-width:1100px){
  .girl {
    display: none;
  }
  .head {
   
    text-align: center;
    margin-left: 150px;
  }
}

@media screen and (min-width:100px) and (max-width:500px){
  .girl {
    display: none;
  }
  .head {
    width: 100%;
    font-size: 18px;
  }
  .main{
    height: 350px;
  }
}