.main_nav {
  box-shadow: rgba(30, 144, 255) 0px 2px 3px;
  background-color: black;
  height: 60px;
  width: 100%;
  overflow: auto;
  color: white;
  position: fixed;
  top: 0;

}
.navbar {
  display: block;
  float: right;
  margin: 5px;
  padding-left: 60%;
}

.box {
  margin: auto;
  text-align: center;
  padding: 10px;
  margin: auto;
}
.box:hover {
  cursor: pointer;
  border-bottom: 3px solid #1e90ff;
}

.resume {
  padding: 10px;
  text-decoration: none;
  border: none;
}

.drawer {
  transition:left 0.5s ease;
 
}

.content {
  height: 200px;
  width: 100%;
  background-color: black;
  color: white;
}
.close {
  border: 3px solid red;
  border: none;
  color: white;
  padding-top: 15px;
  position: absolute;
  padding-left: 380px;
  width: 100%;
}
.body {
  padding-left: 15px;
}

.iconBtn {
  margin: 15px;
  height: 30px;
  width: 30px;
  visibility: hidden;
  margin-left: -30px;
}

.hamburger {
  height: 30px;
  width: 30px;
}

.logo_nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  margin-left: 20px;
  font-style: italic;
  font-family: Georgia, "Times New Roman", Times, serif;
}

@media screen and (min-width: 500px) and (max-width: 1100px) {
  .navbar {
    display: none;
  }
  .iconBtn {
    /* margin: 15px; */
    height: 30px;
    width: 30px;
    visibility: visible;
    margin-left: -30px;
  }
}
@media screen and (min-width: 100px) and (max-width: 500px) {
  .navbar {
    display: none;
  }
  .iconBtn {
    margin: 15px;
    height: 30px;
    width: 30px;
    visibility: visible;
    margin-right: 30px;
  }
}
