.main {
  box-shadow: rgba(30, 144, 255) 0px 5px 10px;
  background-color: black;
  height: 100%;
  width: 100%;
  color: white;
  text-align: center;
  padding: 20px;
}
.skillsDiv > div {
  box-shadow: rgba(30, 144, 255) 0px 4px 6px -1px,
    rgba(30, 144, 255) 0px 2px 4px -1px;
  width: 90%;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  margin-left: 5%;
  padding: 20px;
  margin-top: 30px;
}

.imagesDiv > img {
  height: 100%;
  width: 80%;
}

.Toolhead {
  margin-top: 20px;
}

.ToolsDiv > div {
  box-shadow: rgba(30, 144, 255) 0px 4px 6px -1px,
    rgba(30, 144, 255) 0px 2px 4px -1px;
  width: 95%;
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  gap: 10px;
  padding: 20px;
  margin-top: 30px;
  margin-left: 28px;
}
.gitHead {
  margin-top: 70px;
}
.days {
  margin-top: 20px;
}

.gitCalender {
  box-shadow: rgba(30, 144, 255) 0px 4px 6px -1px,
    rgba(30, 144, 255) 0px 2px 4px -1px;
  margin-top: 30px;
  width: 75%;
  padding-left: 25px;
  margin-left: 180px;
  padding-bottom: 20px;
}

.stats {
  width: 70%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: auto;
  margin-top: 80px;
}
.stats2 {
  margin-top: 60px;
}

.ToolsimagesDiv > img {
  height: 100%;
  width: 90%;
}
.Toolhead {
  margin-top: 50px;
}

@media screen and (min-width: 500px) and (max-width: 1100px) {
  .skillsDiv > div {
    grid-template-columns: repeat(4, 1fr);
    padding-left: 65px;
  }
  .imagesDiv > img {
    margin-top: 20px;
    height: 80%;
    width: 60%;
  }
  .ToolsDiv > div {
    grid-template-columns: repeat(4, 1fr);
    padding-left: 50px;
    width: 93%;
  }
  .ToolsimagesDiv > img {
    height: 85%;
    width: 70%;
  }
  .gitCalender {
    width: 80%;
    margin: auto;
    padding: 30px;
  }
  .githubimage {
    block-size: 13px;
    margin-block: 13px;
  }
  .stats {
    grid-template-columns: repeat(1, 1fr);
    gap: 40px;
  }
  .image {
    width: 70%;
  }
  .stats2 {
    width: 40%;
  }
}

@media screen and (min-width: 100px) and (max-width: 500px) {
  .skillsDiv > div {
    grid-template-columns: repeat(2, 1fr);
    padding-left: 65px;
    padding-bottom: 40px;
  }

  .imagesDiv > img {
    margin-top: 20px;
    height: 90%;
    width: 60%;
    padding: 3px;
  }

  .ToolsDiv > div {
    grid-template-columns: repeat(2, 1fr);
    padding-left: 55px;
    width: 85%;
  }
  .ToolsimagesDiv > img {
    height: 85%;
    width: 70%;
  }
  .gitCalender {
    width: 85%;
    margin: auto;
    padding: 30px;
    font-size: 11px;
  }
  .githubimage {
    block-size: 13px;
    font-size: 14px;
    height: 40%;
  }
  .stats {
    grid-template-columns: repeat(1, 1fr);
    gap: 40px;
  }
  .image {
    width: 90%;
  }
  .stats2 {
    width: 50%;
  }
}
