.main {
  box-shadow: rgba(30, 144, 255) 0px 3px 8px;
  height: 100%;
  width: 100%;
  margin-top: 10px;
  background-color: black;

  padding: 10px;
  padding-bottom: 50px;
}
.mainhead {
  color: white;
  text-align: center;
  margin-top: 30px;
}

.project {
  box-shadow: rgba(30, 144, 255) 0px 3px 8px;
  width: 90%;
  height: 500px;
  margin: auto;
  color: white;
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  justify-content: center;
  gap: 30px;
}

.bloommedplus,
.crystallvills,
.lyst {
  height: 499.7px;
  width: 100%;
  display: block;
}

.description {
  width: 90%;
  height: 490px;
  margin-left: 40px;
  text-align: center;
}

.head {
  margin-top: 10px;
  color: #1e90ff;
}

.prjctDes1 {
  margin-top: 30px;
}

.tech {
  margin-top: 30px;
  color: #1e90ff;
}

.lists {
  border: 3px solid greenyellow;
  width: 50%;
  height: 200px;
  margin: auto;
}

.techs {
  margin-top: 15px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.techs > h4 {
  box-shadow: rgba(30, 144, 255) 0px 3px 8px;
  width: 80%;
  margin: auto;
  padding: 3px;
  margin-top: 20px;
}

.links {
  margin-top: 50px;
}

.githubLink,
.link,
.vdo {
  border: 2px solid #1e90ff;
  background-color: black;
  color: white;
  margin-top: 15px;
  width: 100px;
  height: 40px;
  margin: 20px;
  font-size: 20px;
  cursor: pointer;
}

@media screen and (min-width: 500px) and (max-width: 1100px) {
  .project {
    grid-template-columns: repeat(1, 1fr);
    height: 100%;
  }
}

@media screen and (min-width: 100px) and (max-width: 500px) {
  .project {
    grid-template-columns: repeat(1, 1fr);
    height:850px;
  }
  .bloommedplus,
  .crystallvills,
  .lyst {
    height: 250px;
    width: 100%;
    margin-top: -38px;
  }
  .description {
    margin: auto;
    margin-top: -40px;
  }
  .techs {
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
  }
  .techs > h4 {
    width: 97%;
  }
  
}
